import { configureStore } from '@reduxjs/toolkit';
import { load, save } from 'redux-localstorage-simple';
import { createLogger } from 'redux-logger';
import { fetchAssetData } from './app/reducer';

// import app from './app/reducer';
// import transactions from './transactions/reducer';
// import trove from './troves/reducer';
// import token from './token/reducer';

const PERSISTED_KEYS: string[] = [];

const store = configureStore({
  reducer: {},
  middleware: (getDefaultMiddleware) => {
    const res = getDefaultMiddleware({
      thunk: {
        extraArgument: fetchAssetData,
      },
      serializableCheck: false,
    }).concat(save({ states: PERSISTED_KEYS }));

    return false ? res : res.concat(createLogger());
  },

  preloadedState: load({ states: PERSISTED_KEYS }),
});

export default store;

export type AppState = ReturnType<typeof store.getState>;
export type AppDispatch = typeof store.dispatch;
