import { Configuration, ITokenDetails } from '../utils/inteface';
import zkSyncGoerli from './markets/zkSync-goerli';
import { zkSyncTestnet } from 'wagmi/chains';

const configurations: { [env: string]: Configuration } = {
  ...zkSyncGoerli,
};

export const ConfigChains = [zkSyncTestnet];
export default configurations;

export const getSupportedChains = (): number[] =>
  Object.keys(configurations).map((i) => Number(i));

export const allTokens = (): {
  [chainId: number]: { [key: string]: ITokenDetails };
} => {
  return {
    280: zkSyncGoerli[280].tokens,
  };
};