import { ReactNode } from 'react';
import { Box, BoxProps } from '@chakra-ui/react';

interface IProps {
  children: ReactNode;
}

const Container = (props: IProps & BoxProps) => {
  return (
    <Box px={{ base: 4, md: 6 }} maxWidth={{base: '100%', md: '700'}} width={'max-content'} ml={'auto'} mr={'auto'} {...props}>
      {props.children}
    </Box>
  );
};

export default Container;
