import { createAsyncThunk, createReducer } from '@reduxjs/toolkit';
import axios from 'axios';
import {AssetApiState, LeaderboardState} from '../../utils/inteface';

export const fetchAssetData = createAsyncThunk('app/fetchAssetData', () => {
  return axios
    .get('https://api.onez.cash/prices/assets')
    .then((response) => {
      return {
        'ONEZ': 1,
        'ETH': response.data['collateral-prices']['WETH'],
        ...response.data['collateral-prices']
      }
    })
    .catch((error) => console.log('error fetchAssetData', error));
});

export const fetchLeaderBoard = createAsyncThunk('app/fetchLeaderBoard', () => {
  return axios
    .get('https://api.onez.cash/leaderboard/')
    .then((response) => {
      return {"data": response.data}
    } )
    .catch((error) => console.log('error fetchLeaderBoard', error));
});

interface State {
  assetData: AssetApiState;
  leaderBoardData: LeaderboardState;
}

export const initialState: State = {
  assetData: {},
  leaderBoardData: {data: []},
};

export default createReducer(initialState, (builder) =>
  builder
    .addCase(fetchAssetData.fulfilled, (state, action) => {
      state.assetData = action.payload;
    })
    .addCase(fetchLeaderBoard.fulfilled, (state, action) => {
      // @ts-ignore
      state.leaderBoardData = action.payload;
    })
);
